import { db } from "@/lib/db";
import { currentUser } from "@clerk/nextjs/server";
import { redirect } from "next/navigation";
import Link from "next/link";

export default async function Dashboard() {
  const user = await currentUser();
  if (!user) redirect("/sign-in");

  const funnels = await db.funnel.findMany({ where: { userId: user.id } });
  
  return (
    <div className="p-8 max-w-6xl mx-auto">
        <div className="flex justify-between items-center mb-8">
            <h1 className="text-3xl font-bold">Creator Dashboard</h1>
            <Link href="/dashboard/funnels/new" className="bg-black text-white px-6 py-3 rounded-xl font-bold">+ New Product</Link>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-12">
            <div className="bg-white p-6 rounded-xl border shadow-sm">
                <p className="text-gray-500 text-sm">Active Products</p>
                <p className="text-3xl font-bold">{funnels.length}</p>
            </div>
            <div className="bg-white p-6 rounded-xl border shadow-sm">
                 <p className="text-gray-500 text-sm">Total Revenue</p>
                 <p className="text-3xl font-bold text-green-600">KES 0.00</p>
            </div>
        </div>

        <h2 className="text-xl font-bold mb-4">Your Products</h2>
        {funnels.length === 0 ? (
            <div className="p-12 text-center border rounded-xl bg-gray-50 text-gray-500">No products yet. Create one!</div>
        ) : (
            <ul className="space-y-4">
                {funnels.map(f => (
                    <li key={f.id} className="p-4 border rounded-xl flex justify-between items-center">
                        <span className="font-bold">{f.headline[0]}</span>
                        <Link href={`/f/${f.id}`} className="text-blue-600 hover:underline">View Funnel</Link>
                    </li>
                ))}
            </ul>
        )}
    </div>
  );
}
